@use '../../abstract' as *;

.c-doc-svg {
    position: relative;
    padding: rem(40);
    overflow: hidden;
    border: 2px $white solid;
    border-radius: rem(10);

    svg {
        width: 100%;
        height: 100%;
    }

    &.start-end-svg {
        width: 100%;
        aspect-ratio: 713/467;
    }

    &__loading {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}
