@use '../abstract' as *;

/**
 * HTML font size map.
 */
@include font-size-map;

/**
 * Global columns/gap definition.
 * for testing, for now is overriding below
 */
// @include grid-context;

/**
 * Set up a decent box model on the root element
 */
:root {
    // Instead grid-context
    --gap-size: #{rem(40)};
    --col-size: 9%;

    // Stadard css var
    --header-height: calc(var(--gap-size) * 2);
    --footer-height: calc(var(--gap-size) * 2);
    --nav-width: #{rem(340)};
    --doc-width: none;
    --right-sidebar: none;

    // snippet, global value for height pre-calc.
    --snippet-rem-value: #{strip-unit(rem(14))};
    --snippet-line-height-value: #{strip-unit(rem(18))};

    @include mq(desktop) {
        --doc-width: calc(var(--col-size) * 8);
        --right-sidebar: #{rem(250)};

        @media screen and (height <= 700px) {
            --footer-height: #{rem(50)};
        }
    }

    @include mq(large) {
        --doc-width: calc(var(--col-size) * 8);
    }

    @include mq(xlarge) {
        --doc-width: calc(var(--col-size) * 7);
    }
}

html {
    box-sizing: border-box;

    --scrollbar-color-thumb: white;
    --scrollbar-color-track: black;
    --scrollbar-width: thin;
    --scrollbar-width-legacy: 10px;

    /* Modern browsers with `scrollbar-*` support */
    @supports (scrollbar-width: auto) {
        scrollbar-color: var(--scrollbar-color-thumb)
            var(--scrollbar-color-track);
        scrollbar-width: var(--scrollbar-width);
    }

    /* Legacy browsers with `::-webkit-scrollbar-*` support */
    @supports selector(::-webkit-scrollbar) {
        ::-webkit-scrollbar-thumb {
            background: var(--scrollbar-color-thumb);
        }

        ::-webkit-scrollbar-track {
            background: var(--scrollbar-color-track);
        }

        ::-webkit-scrollbar {
            max-width: var(--scrollbar-width-legacy);
            max-height: var(--scrollbar-width-legacy);
        }
    }
}

/**
 * Esplcit box-sizing ( no inherit ).
 * Solve web component box-sizing problem.
 */
*,
*::after,
*::before {
    box-sizing: border-box;
}

body {
    position: relative;
    overflow-x: hidden;
    background: $white;

    &::after {
        content: '';
        visibility: hidden;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transition:
            opacity 0.55s,
            visibility 0.55s;
        opacity: 0;
        background:
            url('data:image/png;base64,  nO3Wp/3fEV5a+4wz//6qy8JxjZsmxxy5+4w9CDNJY09T072iKG0EnOS0arEYgXqYnXcYHwjTtUNAcMelOd4xpkoqiTYICWFq0JSiPfPDQdnt+4/wuqcXY47QILbgAAAABJRU5ErkJggg=='),
            radial-gradient(
                circle,
                rgb(232 232 232 / 100%) 0%,
                rgb(166 182 185) 93%
            );
    }

    &[data-template='home'],
    &[data-template='template-listing'],
    &[data-template='template-animation'] {
        &::after {
            visibility: visible;
            opacity: 1;
        }
    }

    &[data-template='about'] {
        background: $grey;

        &::after {
            visibility: visible;
            opacity: 1;
        }
    }
}

#content {
    position: relative;
}

/**
 * Basic styles for links
 */

img {
    max-width: 100%;
    height: auto;
}

main {
    position: relative;
    z-index: 1;
    min-height: 100vh;
    padding-top: calc(var(--header-height));
    transition: opacity 0.55s;

    @include when-inside('[data-template="home"]') {
        padding-top: 0;
    }

    @include when-inside('[data-template="template-animation"]') {
        padding-top: 0;
    }

    &.shift {
        opacity: 0.1;
        cursor: pointer;
    }
}

.container {
    margin: auto;
    padding-left: var(--gap-size);

    @include when-inside('[data-template="template-animation"]') {
        padding-left: 0;
    }
}

.l-padding {
    padding: var(--gap-size);
}
